export default () => {
  const btns = document.querySelectorAll('.js--amount-btn');

  if (btns.length === 0) {
    return;
  }

  btns.forEach((btn) => {
    const minusBtn = btn.querySelector('.js--amount-btn-minus');
    const plusBtn = btn.querySelector('.js--amount-btn-plus');
    const numberContainer = btn.querySelector('.js--amount-btn-number');

    minusBtn.addEventListener('click', () => {
      if (numberContainer.innerHTML > 0) {
        numberContainer.innerHTML = parseInt(numberContainer.innerHTML) - 1; /* eslint-disable-line radix */ /* eslint-disable-line max-len */
      }
    });

    plusBtn.addEventListener('click', () => {
      if (numberContainer.innerHTML < 100) {
        numberContainer.innerHTML = parseInt(numberContainer.innerHTML) + 1; /* eslint-disable-line radix */ /* eslint-disable-line max-len */
      }
    });
  });
};
