export default () => {
  const containers = document.querySelectorAll('.js--search-container');

  if (containers.length === 0) {
    return;
  }

  containers.forEach((container) => {
    const openBtn = container.querySelector('.js--search-open');
    const closeBtn = container.querySelector('.js--search-close');
    const form = container.querySelector('.js--form');

    form.addEventListener('submit', (e) => {
      e.preventDefault();
    });

    openBtn.addEventListener('click', () => {
      container.classList.add('searching');

      closeBtn.addEventListener('click', () => {
        container.classList.remove('searching');
        form.reset();
      });
    });
  });
};
