export default () => {
  const upBtns = document.querySelectorAll('.js--up');

  if (upBtns.length === 0) {
    return;
  }

  upBtns.forEach((upBtn) => {
    upBtn.addEventListener('click', (e) => {
      e.preventDefault();

      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    });
  });
};
