import Swiper, { Navigation, Scrollbar, EffectFade } from 'swiper';

export default () => {
  Swiper.use([Navigation, Scrollbar, EffectFade]);

  const bigSliders = document.querySelectorAll('.js--big-slider');

  bigSliders.forEach((slider) => {
    const swiper = new Swiper(slider, { /* eslint-disable-line no-unused-vars */
      effect: 'fade',
      modules: [Navigation, Scrollbar],
      speed: 100,
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
      scrollbar: {
        el: '.swiper-scrollbar',
      },
    });
  });
};
