import SimpleBar from 'simplebar'; /* eslint-disable-line no-unused-vars */

export default () => {
  const body = document.querySelector('body');
  const openBtn = body.querySelector('.js--burger-open');
  const closeBtn = body.querySelector('.js--burger-close');
  const menu = body.querySelector('.js--menu');

  if (!openBtn || !menu || !closeBtn) {
    return;
  }

  const cover = menu.querySelector('.js--menu-cover');
  const firstLevelItems = menu.querySelectorAll('.js--first-level-item');
  const backBtn = menu.querySelector('.js--back-menu');

  if (!cover || !firstLevelItems || !backBtn) {
    return;
  }

  function closeMenu() {
    menu.classList.remove('open', 'second-level-menu', 'third-level-menu');
    openBtn.classList.remove('active');
    closeBtn.classList.remove('active');
    body.classList.remove('stop-scrolling');
  }

  function openMenu() {
    menu.classList.add('open');
    openBtn.classList.add('active');
    closeBtn.classList.add('active');
    body.classList.add('stop-scrolling');
  }

  openBtn.addEventListener('click', () => {
    openMenu();

    closeBtn.addEventListener('click', () => {
      closeMenu();
    });

    cover.addEventListener('click', () => {
      closeMenu();
    });

    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        closeMenu();
      }
    });

    firstLevelItems.forEach((firstLevelItem) => {
      firstLevelItem.onclick = function openSecondLevel(e) { /* eslint-disable-line no-param-reassign */ /* eslint-disable-line max-len */
        const secondLevelItems = firstLevelItem.querySelectorAll('.js--second-level-item');
        const thirdLevelLists = firstLevelItem.querySelectorAll('.js--third-level-list');

        if (e.target.closest('.js--to-second-level')) {
          if (!menu.classList.contains('second-level-menu')) {
            menu.classList.add('second-level-menu');
          } else if (menu.classList.contains('second-level-menu')) {
            menu.classList.remove('third-level-menu');
            menu.classList.remove('second-level-menu');
          }
        } else {
          e.preventDefault();
        }

        secondLevelItems.forEach((secondLevelItem) => {
          secondLevelItem.addEventListener('click', () => {
            const thirdActiveList = thirdLevelLists[Array.from(secondLevelItems).indexOf(secondLevelItem)]; /* eslint-disable-line max-len */
            secondLevelItems.forEach((item) => {
              item.classList.remove('active');
              thirdLevelLists[Array.from(secondLevelItems).indexOf(item)].classList.remove('active');
            });

            menu.classList.add('third-level-menu');
            secondLevelItem.classList.add('active');
            thirdActiveList.classList.add('active');

            thirdActiveList.querySelector('.js--back-to-second-level').addEventListener('click', () => {
              menu.classList.remove('third-level-menu');
              secondLevelItem.classList.remove('active');
              thirdActiveList.classList.remove('active');
            });
          });
        });

        backBtn.addEventListener('click', () => {
          menu.classList.remove('second-level-menu', 'third-level-menu');
          secondLevelItems.forEach((secondLevelItem) => {
            secondLevelItem.classList.remove('active');
            thirdLevelLists[Array.from(secondLevelItems).indexOf(secondLevelItem)].classList.remove('active');
          });
        });
      };
    });
  });
};
